.container {
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
  
  
}

  

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content:space-between;
  border: 50px;
  border-style: solid;
  
}

.card { /* Adjust as needed for spacing */
  margin-bottom: 20px; /* Adjust as needed for spacing */
  background-color:goldenrod;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.5s ease;
  width: 280px;
  height: 400px;
}

.card:hover {
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2), /* Original shadow */
              0 16px 18px rgba(82, 2, 2, 0.2); /* Additional shadow */
}


.card-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.card-text {
  font-size: 1rem;
  margin-bottom: 0.3rem;
}

.button {
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #007bff;
  color:chartreuse;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  justify-content: space-between;
  text-align: center;
  transition: background-color 0.3s ease;
  
}

.button:hover {
  background-color: #0056b3;
}

